import React, { useRef, Suspense, useState } from "react";
import Loader from "../components/Loader";
import JsonData from "../json/data.json";
import TransitionLink from "../hooks/TransitionLink";
import GlassButton from "../components/GlassButton";
const GlassBtn = ({ title, tagLine, data }) => {
  const dialogRef = useRef();
  const [broken, setBroken] = useState(false);
  return (
    <>
      <GlassButton
        onClick={() => (data ? dialogRef.current.showModal() : setBroken(true))}
        className={`btn_glass_purple_parent w-full ${
          broken ? "broken_glass" : ""
        }`}
      >
        <span className="">{title}</span>
      </GlassButton>

      <dialog
        ref={dialogRef}
        className="transition-all rounded-lg z-10 bg-white/30 p-10 backdrop-blur-2xl min-w-[325px]"
      >
        <div className=" flex flex-col items-center">
          <form method="dialog" className="absolute top-0 right-0 mr-2 mt-2">
            <GlassButton className="btn_glass_purple_parent p-4 ">
              X
            </GlassButton>
          </form>
          <p className="text-2xl Anuphan text-gradB">{tagLine}</p>
          <ul className="flex flex-col my-5 w-full">
            {data &&
              data.map(({ title, route }, index) => (
                <li className="btn_glass_purple_parent " key={index}>
                  <TransitionLink
                    to={route}
                    className="btn_glass_purple gradient_purple_text rounded-lg"
                  >
                    {title}
                  </TransitionLink>
                </li>
              ))}
          </ul>
        </div>
      </dialog>
    </>
  );
};
const Collection = () => {
  return (
    <Suspense fallback={<Loader />}>
      <ul className="flex flex-wrap justify-between cursor-pointer p-4 gap-2">
        {JsonData.projects.map((project, index) => (
          <li className="flex-[45%]" key={index}>
            <GlassBtn {...project} />
          </li>
        ))}
      </ul>
    </Suspense>
  );
};

export default Collection;
